import chroma from "chroma-js";

export default class ColorGame {
  constructor(mode) {
    this.mode = mode;
    this.colorCount = this.getModeColorCount();
    this.colors = this.generateColors();
    this.pickedColor =
      this.colors[Math.floor(Math.random() * this.colors.length)];
  }

  getModeColorCount() {
    switch (this.mode.toUpperCase()) {
      case "EASY":
        return 3;
      case "HARD":
        return 6;
      default:
        return 0;
    }
  }

  generateColors() {
    const colors = [];
    for (let i = 0; i < this.colorCount; i++) {
      colors.push(chroma.random().css()); //rgb(x,x,x)
    }
    return colors;
  }

  checkColor(colorRgb) {
    return chroma(colorRgb).hex() === chroma(this.pickedColor).hex();
  }
}
