import ColorGame from "./ColorGame";
import ColorBlock from "./Components/ColorBlock";

const colorText = document.querySelector("#color-display");
const newGameButton = document.querySelector("#reset");
const modeButtons = document.querySelectorAll("button.mode");
const colorsContainer = document.querySelector("#container");
const messageText = document.querySelector("#message");

const colorGame = new ColorGame("easy");
window.colorGame = colorGame;
initGame();

function initGame() {
  colorText.textContent = window.colorGame.pickedColor;
  messageText.innerText = "";
  colorsContainer.innerHTML = window.colorGame.colors
    .map((color) => ColorBlock(color))
    .join("\n");
  Array.from(colorsContainer.children).forEach((colorBlock) => {
    colorBlock.addEventListener("click", () => {
      console.log(colorBlock.style.backgroundColor);
      if (window.colorGame.checkColor(colorBlock.style.backgroundColor)) {
        messageText.innerText = "Correct!!!";
      } else {
        messageText.innerText = "Wrong!!!";
      }
    });
  });
}

newGameButton.addEventListener("click", () => {
  window.colorGame = new ColorGame(window.colorGame.mode);
  initGame();
});

modeButtons.forEach((btn) => {
  btn.addEventListener("click", () => {
    modeButtons.forEach((btn) => btn.classList.remove("selected"));
    btn.classList.add("selected");
    window.colorGame = new ColorGame(btn.textContent);
    initGame();
  });
});
